import React from 'react'
import { motion } from "framer-motion"
import styled from "styled-components"

const Space = styled.div`
  flex: 1 1 auto;
`
class Group extends React.Component {
  render () {
    return (
      <GroupS {...this.props} initial="enter">
        {this.props.children}
      </GroupS>
    )
  }
}

const InnerStyle = (props) => {
  return`
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    flex-direction: row;
    .group, .group-inner {
     display: flex;
     flex-direction: row;
     align-content: flex-start;
     flex: 0 0 auto;
     margin: 0;
     width: auto;
     &:last-child {
       margin-right: -1px;
     }
     &.space,
     &.stretch,
     .space,
     .stretch,
     &[space],
     [space] {
       flex: 1 1 auto;
       margin: 0;
     }
   }
   &.space,
   &.stretch,
   .space,
   .stretch,
   &.space,
   [space] {
     flex: 1 1 auto;
     margin: 0;
   }
   &.keypad,
   &[keypad] {
     display: grid;
     grid-template-columns: repeat(2, 1fr);
     gap: 0.5rem;
     .btn, button {
       align-self: stretch;
     }
   }
`
}

const GroupStyle = (props) => {
  return`
    .group {
      ${InnerStyle(props)}
    }
  `
}

const GroupS = styled(motion.div)`
  ${ props => InnerStyle(props.theme) }
`

export {Group, GroupStyle, Space};
