import React from 'react'
import { Link } from 'react-router-dom'
import { Wizard, Steps, Step } from 'react-albus'
import report from '../images/onboarding/ilust/missing-report.png'
import { I18n } from 'react-i18nify-lite'

export default class Activation extends React.Component {

  constructor(props){
    super(props)
    this.state = {
      data: {}
    }
  }

  openCustomURLinIFrame = (src)=> {
      var rootElm = document.documentElement;
      var newFrameElm = document.createElement("IFRAME");
      newFrameElm.setAttribute("src",src);
      rootElm.appendChild(newFrameElm);
      //remove the frame now
      newFrameElm.parentNode.removeChild(newFrameElm);
  }

  render() {
    return <div className="activation fs-onboarding regular inverted">
    <div className="column">
      <Wizard>
        <Steps>
          <Step id="try" render={({ next }) => (
              <div className="container">
                <div id="done" className="column">
                  <h4 className="tac">{I18n.t("activation.you_ready")}</h4>
                  <figure className="anime uno">
                    <figure className="anime dos">
                      <figure className="anime tres">
                        <i className="icon-shield-4"/>
                      </figure>
                    </figure>
                  </figure>
                  <h2>
                    <small>{I18n.t("activation.status")}</small>
                    <span id="txtStatusDevice">{I18n.t("activation.protected")}</span>
                  </h2>
                </div>
                <div id="final" className="step">
                  <h4 className="tac">{I18n.t("activation.try_report")}</h4>
                  <figure>
                    <div className="bg" />
                    <img src={report} alt="" className="first" />
                  </figure>
                  <p>{I18n.t("activation.evidence")}</p>
                  <div className="button-group">
                    <Link to={`/${I18n._locale}/`}  className="btn alt" >{I18n.t("activation.button_skip")}</Link>
                    <button className="btn success icon-right" onClick={(e) => this.openCustomURLinIFrame("iosreportexample://")}>{I18n.t("activation.button_next")}</button>
                  </div>
                </div>
              </div>
            )}
          />
        </Steps>
      </Wizard>
    </div>
  </div>
  }
}
