import React from 'react'
import { Link } from 'react-router-dom'
import logomono from '../images/branding/prey-logo-monotono-alt.svg'
import { I18n } from 'react-i18nify-lite'

export default class Login extends React.Component {

  constructor(props){
    super(props)
    this.state = {
      data: {},
      passwordType: 'password',
    }
  }

  errorsFor = (name)=>{
      const errors = this.state.data[name]
      if(!errors) return
      if(errors.length === 0) return

      return <span className="msg info-error">{
        errors.map((o)=>(
            <span>{o}</span>
          )
        )}
        </span>
  }

  handleClickLogin = (e)=>{
    var emaillogin=encodeURIComponent(document.getElementById('emaillogin').value)
    var passlogin=encodeURIComponent(document.getElementById('passlogin').value)
    var url = 'ioslogin://parameters?preyEmailLogin='+emaillogin+'&preyPassLogin='+passlogin;
    this.openCustomURLinIFrame(url);
  }

  handleFocus = (e) => {
    this.setState({data: {}})
  }

  handleShowPassword = (e) => {
    var type=this.state.passwordType
    if (type === 'password') {
      this.setState({
        passwordType: 'text'
      })
    } else {
      this.setState({
        passwordType: 'password'
      })
    }
  }

  handleClickQR = (e)=>{
      var url = 'iosqrcode://';
      this.openCustomURLinIFrame(url);
  }

  handleShowTerms = (e)=>{
      var url = 'iosterms://';
      this.openCustomURLinIFrame(url);
  }

  handleShowPrivacy = (e)=>{
      var url = 'iosprivacy://';
      this.openCustomURLinIFrame(url);
  }

  handleShowForgot = (e)=>{
      var url = 'iosforgot://';
      this.openCustomURLinIFrame(url);
  }

  openCustomURLinIFrame = (src)=> {
      var rootElm = document.documentElement;
      var newFrameElm = document.createElement("IFRAME");
      newFrameElm.setAttribute("src",src);
      rootElm.appendChild(newFrameElm);
      //remove the frame now
      newFrameElm.parentNode.removeChild(newFrameElm);
  }


  render() {
    return  <div className="login fs-onboarding regular inverted">
    <div className="column">
      <div className="sign-form">
        <form autoComplete="off" className="form form-session" id="new-session" acceptCharset="UTF-8" onFocus={this.handleFocus}>
          <h1>{I18n.t("signin.title")}</h1>
          <p className="lead">{I18n.t("signin.subtitle")}</p>
          <ul className="form vvv">
            <li>
              <label htmlFor="emaillogin">{I18n.t("signin.field1")}</label>
              <input id="emaillogin" placeholder=" " type="email"  tabIndex="1" onFocus={this.handleFocus}  />
            </li>
          </ul>
          <ul className="form hvv">
            <li className="superpassword">
              <label className="tag-label" htmlFor="passlogin">{I18n.t("signin.field2")}
               <input type={this.state.passwordType} name="password1" id="passlogin" tabIndex="2" onFocus={this.handleFocus}  onClick={this.touch} />
               <a onClick={this.handleShowPassword}><p className={`btn s square ${this.state.passwordType === 'text' ? 'success':''}`}><i className="icon-eye" /></p></a>
              </label>
            </li>
          </ul>
          <br/>
          {this.errorsFor("error")}
         
            <Link tabIndex="3" to="#" onClick={this.handleClickLogin} className="btn cta">{I18n.t('signin.button_submit')}</Link>
            <p/>
              <Link tabIndex="4" to="#" onClick={this.handleClickQR} className="btn alt icon-qr-code">{I18n.t('signin.button_qr')} &nbsp;</Link>
            
        </form>

          
          <Link className="btn-link btn-block" to={`/${I18n._locale}/signup`}>{I18n.t('signin.button_account')}</Link>
          <a className="btn-link btn-block" onClick={this.handleShowForgot}>{I18n.t('button_forgot')}</a>
      </div>
      <div id="footer">
        <footer className="copy">
          <div className="footer-links">
            <a onClick={this.handleShowTerms} rel="noopener noreferrer">{I18n.t('button_terms')}</a>
            <a onClick={this.handleShowPrivacy} rel="noopener noreferrer">{I18n.t('button_privacy')}</a>
            <a href="https://www.preyproject.com/privacy#cookies" target="_blank" rel="noopener noreferrer">{I18n.t('button_cookies')}</a>
          </div>
          <img src={logomono} alt="Prey" className="logo" /> 
        </footer>
      </div>
    </div>
  </div>
 }

}
