import React, { useState } from 'react'
import styled from 'styled-components'
import { motion, AnimatePresence } from 'framer-motion'
import { Button } from '../Button/Button'
import { composeRefs, transition } from '../Segundo'
import ResizeObserver from 'resize-observer-polyfill'
import { shade } from 'polished'
import { ToggleLayer } from 'react-laag'

export const variantsFold = {
  open: { y: 0, opacity: 1, height: "auto", transition: {...transition} },
  collapsed: { y: -100, opacity: 0, height: 0, transition: {...transition} },
  exit: { y: -100, opacity: 0, height: 0, transition }
}
export const variantsFoldItem = {
  initial: { opacity: 0, transition },
  exit: { opacity: 0, transition },
  enter: { opacity: 1, transition }
};

const InnerStyle = (props) => {
  return `
    background: ${props.bg}
    border: 1px solid ${props.linkColor};
    box-shadow: 0.5rem 0.5rem 0 ${shade(0.6, props.linkColor)};
  `
}

export const FolderStyle = (props) => {
  return `
    .folder {
      ${InnerStyle(props)}
    }
`}

const Drawer = styled(motion.div)`
  ${ props => InnerStyle(props.theme) }
`

export const Folder = React.forwardRef(function Folder(props, ref) {
  const prevChildren = props && props.children && props.children.props
  const prevChildrenClassName = props.className
  // const hasDropArrow = props.hasDropArrow || false
  const buildLabel = (label) => {
    return {
      __html: label
    };
  }
  return (
    <ToggleLayer
      ResizeObserver={ResizeObserver}
      renderLayer={props => {
        const rotation = {
          top: { rotateX: "90deg" },
          bottom: { rotateX: "-90deg" },
          left: { rotateY: "90deg" },
          right: { rotateY: "-90deg" }
        };

        const transformOrigin = {
          top: "bottom",
          left: "right",
          bottom: "top",
          right: "left"
        };

        return (
          <AnimatePresence>
            {props.isOpen ? (
              <Drawer
                key="layer"
                ref={props.layerProps.ref}
                style={{
                  ...props.layerProps.style,
                  width: '100%',
                  maxWidth: 300,
                  zIndex: '10000',
                  transformOrigin: transformOrigin[props.layerSide]
                }}
                initial={{
                  opacity: 0,
                  scale: 0.9,
                  ...rotation[props.layerSide]
                }}
                animate={{
                  opacity: 1,
                  scale: 1,
                  rotateX: `0deg`,
                  rotateY: "0deg"
                }}
                exit={{
                  opacity: 0,
                  scale: 0.9,
                  ...rotation[props.layerSide]
                }}
                transition={{
                  type: "spring",
                  stiffness: 400,
                  damping: 30
                }}
                className={`${prevChildrenClassName}`}
              >
                {prevChildren.children}
              </Drawer>
            ) : null}
          </AnimatePresence>
        );
      }}
      closeOnOutsideClick
      placement={{
        anchor: "BOTTOM_LEFT",
        autoAdjust: true,
        snapToAnchor: false,
        triggerOffset: 0,
        scrollOffset: 10,
        preferY: "BOTTOM"
      }}
    >
      {({ isOpen, triggerRef, toggle }) => (
        <motion.div
          ref={composeRefs(triggerRef, ref)}
          onClick={toggle}
          style={props.style}
          id={props.id}
          className={`wrap-${prevChildrenClassName} folder-custom`}
        >
          {props.epigraph ? (<p>{props.epigraph}</p>) : ''}
          <Button className={`${props.button} ${isOpen ? 'active' : ''}`} dangerouslySetInnerHTML={buildLabel(props.label)} />
        </motion.div>
      )}
    </ToggleLayer>
  );
});

export const Fold = (props) =>{
  const [expanded, setExpanded] = useState(false);

  return(
    <motion.div {...props}
      variants={variantsFold}
      exit="exit"
    >
      <Button
        onClick={() => setExpanded(!expanded)}
        className={`${!expanded ? 'icon-down-open' : 'icon-up-open'} ${props.button}`}
      >
        {props.label}
      </Button>
      <AnimatePresence exitBeforeEnter>
        {expanded ? (
          <motion.div
            initial="initial"
            enter="enter"
            exit="exit"
            animate={expanded ? 'enter' : 'exit'}
            variants={variantsFoldItem}>
            {props.children}
          </motion.div>
        ) : null }
      </AnimatePresence>

    </motion.div>
  )
}
