import React from 'react'
import styled from 'styled-components'
import { transparentize, lighten } from 'polished'

const SetFeedback = (props) => {
  return`
    background-image: url("data:image/svg+xml,%3Csvg width='6' height='6' viewBox='0 0 6 6' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='%23${props.replace('#','')}' fill-opacity='0.1' fill-rule='evenodd'%3E%3Cpath d='M5 0h1L0 6V5zM6 5v1H5z'/%3E%3C/g%3E%3C/svg%3E");
    background-size: 5px;
    background-color: ${transparentize(0.6, props)};
    border: 0.1rem solid ${props};
    line-height: 1;
    color: ${lighten(0.3, props)};
    a {
      color: ${lighten(0.4, props)};
      display: inline;
      font-weight: bold;
      opacity: 0.70;
      text-decoration: underline;
      &:hover {
        color: ${lighten(0.1, props)};
      }
    }
  `
}

const FeedbackStyle = (props) => {
  return`
    .info, .information {
      ${SetFeedback(props.blue)}
    }
    .info-error {
      ${SetFeedback(props.red)}
    }
    .info-success {
      ${SetFeedback(props.green)}
    }
    .info-notice {
      ${SetFeedback(props.orange)}
    }
    .info-warning {
      ${SetFeedback(props.yellow)}
    }
    .info-critical {
      ${SetFeedback(props.purple)}
    }
    .text-error {
      color: ${lighten(0.3, props.red)};
      a {
        color: ${lighten(0.4, props.red)};
        &:hover { color: ${lighten(0.1, props.red)}; }
      }
    }
    .text-warning {
      color: ${lighten(0.3, props.yellow)};
      a {
        color: ${lighten(0.4, props.yellow)};
        &:hover { color: ${lighten(0.1, props.yellow)}; }
      }
    }
    .text-success {
      color: ${lighten(0.3, props.green)};
      a {
        color: ${lighten(0.4, props.green)};
        &:hover { color: ${lighten(0.1, props.green)}; }
      }
    }
    .text-notice {
      color: ${lighten(0.3, props.orange)};
      a {
        color: ${lighten(0.4, props.orange)};
        &:hover { color: ${lighten(0.1, props.orange)}; }
      }
    }
    .text-critical {
      color: ${lighten(0.3, props.purple)};
      a {
        color: ${lighten(0.4, props.purple)};
        &:hover { color: ${lighten(0.1, props.purple)}; }
      }
    }
    .transparent,
    [class^="text-"],
    [class*=" text-"] {
      background: transparent !important;
      box-shadow: 0 0 0 0 transparent inset !important;
    }
  `
}

const FeedbackS = styled.div`
  ${ props => SetFeedback( Object.assign( {}, props.theme, props)  ) }
`

class Feedback extends React.Component {

  render () {
    return (
      <FeedbackS {...this.props}>
        {this.props.children}
      </FeedbackS>
    )
  }

}

export {Feedback, FeedbackStyle}
