import React from 'react'
import { I18n } from 'react-i18nify-lite'
import { Group, Space } from 'prey-stash'
import { Link } from 'react-router-dom'
import { Dialog } from '../assets/Dialog'

class EmailSent extends React.Component {

  constructor(props){
    super(props)
    this.state = {
      data: {},
      EmailValidation: false,
      EmailExpired: false,
      EmailConfirm: false,
      emailValue: ''
    }
  }

  handleResend = (ev) => {
    ev.preventDefault()
    var email=encodeURIComponent(document.getElementById('userEmail').value);
    var url = 'iosemailresend://parameters?emailSignup='+email;
    this.openCustomURLinIFrame(url);
  }

  openCustomURLinIFrame = (src)=> {
      var rootElm = document.documentElement;
      var newFrameElm = document.createElement("IFRAME");
      newFrameElm.setAttribute("src",src);
      rootElm.appendChild(newFrameElm);
      //remove the frame now
      newFrameElm.parentNode.removeChild(newFrameElm);
  }

  handleEmailValidation = (e) => {
    e.preventDefault()
    this.setState({
      EmailValidation: true,
      emailValue: document.getElementById('userEmail').value
    })
  }

  handleEmailExpired = (e) => {
    e.preventDefault()
    this.setState({
      EmailExpired: true
    })
  }

  handleEmailConfirm = (e) => {
    e.preventDefault()
    this.setState({
      EmailConfirm: true
    })
  }

  render() {
    return (
      <>
        <div className="fs-onboarding regular inverted">
          <div className="column">
            <h4 className="heading">{I18n.t(`email_validation.sent.title`)}</h4>
            <p>{I18n.t(`email_validation.confirm.body_a`)}</p>
            <label>
              <span>{I18n.t(`email_validation.sent.mail`)}</span>
              <input id="userEmail" type="email" style={{ width: '100%' }} />
            </label>
            <p>{I18n.t(`email_validation.confirm.body_b`)}</p>
          </div>
        </div>
        <a onClick={this.handleResend} href="#/" className="btn fill success icon-right-open-big poa">{I18n.t(`resend`)}</a>
        <button id="btnIDEmailValidation" className="hidden" onClick={this.handleEmailValidation}></button>
        <button id="btnIDEmailExpired" className="hidden" onClick={this.handleEmailExpired}></button>
        <button id="btnIDEmailConfirm" className="hidden" onClick={this.handleEmailConfirm}></button>

        {this.state.EmailValidation ? (
          <Dialog
            className={`dialog`}
            >
            <div className="inner" key={'pin'}>
              <div className="content">
                <h5>{I18n.t(`email_validation.sent.title`)}</h5>
                <p>{I18n.t(`email_validation.sent.dialog_title`)} <b>{this.state.emailValue}</b> {I18n.t(`email_validation.sent.dialog_body`)}</p>
              </div>
              <Group>
                <Group>
                  <a onClick={() => this.setState({EmailValidation: false})} className="btn">{I18n.t(`send`)}</a>
                </Group>
              </Group>
            </div>
          </Dialog>
        ): null}

        {this.state.EmailExpired ? (
            <Dialog
              className={`dialog`}
              >
              <div className="inner" key={'pin'}>
                <div className="content">
                  <h5>{I18n.t(`email_validation.expired.title`)}</h5>
                  <p>{I18n.t(`email_validation.expired.body`)}</p>
                </div>
                <Group>
                  <Group>
                    <Link to={`/${I18n._locale}/signup`} className="btn">{I18n.t(`email_validation.expired.button`)}</Link>
                  </Group>
                </Group>
              </div>
            </Dialog>
          ): null
        }
        {
          this.state.EmailConfirm ? (
            <Dialog
              className={`dialog`}
              >
              <div className="inner" key={'pin'}>
                <div className="content">
                  <h5>{I18n.t(`email_validation.ok.title`)}</h5>
                  <p>{I18n.t(`email_validation.ok.body`)}</p>
                </div>
                <Group>
                  <Group>
                    <Link to={`/${I18n._locale}/permissions`} className="btn">{I18n.t(`email_validation.ok.button`)}</Link>
                  </Group>
                </Group>
              </div>
            </Dialog>
          ): null
        }
      </>
    )
  }
}

export default EmailSent
