import React from 'react'
import logomono from '../images/branding/prey-logo-monotono-alt.svg'
import { I18n } from 'react-i18nify-lite'
import { Link } from 'react-router-dom'
import { Group } from 'prey-stash'
import { Dialog, Loading } from '../assets/Dialog'

export default class Signup extends React.Component {

  constructor(props){
    super(props)
    this.state = {
      data: {},
      isLoading: false,
      isEmailOk: false,
      mail:'',
      passwordType: 'password',
      capsLockOn: false,
    }
  }

  errorsFor = (name) => {
    const errors = this.state.data[name]
    if(!errors) return
    if(errors.length === 0) return

    return <div ref='error' className="info info-error">{
      errors.map((o,i)=>(
          <p key={i}>{o}</p>
        )
      )}
      </div>
  }

  handleFocus = (e) => {
    this.setState({data: {}})
  }

  handleClick = (e)=>{
     var email=document.getElementById('email').value;
     if(email!==''){
        this.setState({
          mail: email,
          isEmailOk: true,
        })
     }else{
        this.setState({
          data: {} ,
          isLoading: true
          }, ()=>{setTimeout(this.callHandleClick, 0)
        })
     }
  }

  handleModalCancel = (e)=>{
    this.setState({
      isEmailOk: false,
    })
  }

  handleClick2 = (e)=>{
    this.setState({
      data: {} ,
      isLoading: true
      }, ()=>{setTimeout(this.callHandleClick, 0)
    })
  }
  handleShowPassword = (e) => {
    var type=this.state.passwordType
    if (type === 'password') {
      this.setState({
        passwordType: 'text'
      })
    } else {
      this.setState({
        passwordType: 'password'
      })
    }
  }

  verificateTab = (counter) =>{
    counter=counter-1;
    this.changeTab()
    if(counter>0){
      if(window.Android){
        setTimeout(
          () => this.verificateTab(counter),
          1000
        );
      }else{
        alert("verificateTab:"+counter);
        setTimeout(
          () => this.verificateTab(counter),
          1000
        )
      }
    }
  }

  touch = () => {
    if(window.Android){
      window.Android.touch();
    }
    this.setState({
        capsLockOn: false
    });
    if(window.Android){
      this.verificateTab(20)
    }else{
      this.verificateTab(1)
    }
  }
  changeTab = () => {
    var capsLock=this.state.capsLockOn
    if(window.Android){
      capsLock=window.Android.capsLockOn();
    }else{
      capsLock=!capsLock;
    }
    this.setState({
      capsLockOn: capsLock
    })
  }

  callHandleClick = (e)=>{
    var name=document.getElementById('name').value;
    var email=document.getElementById('email').value;
    var password1=document.getElementById('password1').value;
    var password2=document.getElementById('password2').value;
    var policy_rule_age=document.getElementById('user[policy_rule_age]').checked;
    var policy_rule_privacy_terms=document.getElementById('user[policy_rule_privacy_terms]').checked;
    var offers=document.getElementById('user[offers]').checked;

    if(window.Android){
      let json=window.Android.signup(''+name,''+email,''+password1,''+password2,''+policy_rule_age,''+policy_rule_privacy_terms,''+offers);
      if(json!==''){
        this.setState({
          isEmailOk: false,
          data: JSON.parse(json),
          isLoading: false
        })
      }else{
        this.props.history.push(`/${I18n._locale}/emailsent`);
      }
    } else {
      //let json='{"password":["no puede estar en blanco"],"password_confirmation":["no coincide con la confirmacin","no puede estar en blanco","es demasiado corto (el mnimo es de 6 caracteres)"],"policy_rule_age":["necesita confirmacin"],"policy_rule_privacy_terms":["necesita confirmacin"],"name":["no puede estar en blanco"],"email":["es demasiado corto (el mnimo es de 6 caracteres)","debera ser un email"]}';
      //let json='{"error":["error comunication"]}';
      let json='';
      if(json!==''){
        this.setState({
          isEmailOk: false,
          data: JSON.parse(json),
          isLoading: false
        })
      }else{
        this.props.history.push(`/${I18n._locale}/emailsent`);
      }
      //alert("ola oso signup name:"+name+" email:"+email+" password1:"+password1+" password2:"+password2+" policy_rule_age:"+policy_rule_age+"  policy_rule_privacy_terms:"+policy_rule_privacy_terms );;
    }
  }
  render() {
    return <div className="inner-content">
      <div className="content">
        <div className="login fs-onboarding regular inverted">
          <div className="column">
            <div className="sign-form">


              <form autoComplete="off" className="form form-session" id="new-session" acceptCharset="UTF-8">



                <h1>{I18n.t("signup.title")}</h1>
                <p className="lead">{I18n.t("signup.subtitle")}</p>
                <ul className="form vvv">
                  <li>
                    <label htmlFor="name">{I18n.t("signup.field1")}</label>
                    <input id="name" name="name" required type="text" tabIndex="1" onFocus={this.handleFocus}  />
                    {this.errorsFor("name")}
                  </li>
                  <li>
                    <label htmlFor="email">{I18n.t("signup.field2")}</label>
                    <input id="email" name="email" required type="email" tabIndex="2" onFocus={this.handleFocus} />
                    {this.errorsFor("email")}
                  </li>
                </ul>
                <ul className="form hvv">
                  <li className="superpassword">
                    <label className="tag-label" htmlFor="password1">{I18n.t("signup.field3")}
                      <input type={this.state.passwordType} name="password1" id="password1" tabIndex="3" onFocus={this.handleFocus}  onClick={this.touch} />
                      <a onClick={this.handleShowPassword}><p className={`btn s square ${this.state.passwordType === 'text' ? 'success':''}`}><i className="icon-eye" /></p></a>
                    </label>
                    {
                      this.state.capsLockOn ? (
                        <div className="info info-warning"><p>{I18n.t("signup.capsLockIsOn")}</p></div>
                      ) : null
                    }
                    {this.errorsFor("password")}
                  </li>
                  <li className="superpassword">
                    <label className="tag-label" htmlFor="password2">{I18n.t("signup.field4")}
                      <input type={this.state.passwordType} name="password2" id="password2" tabIndex="4" onFocus={this.handleFocus}  onClick={this.touch} />
                      <a onClick={this.handleShowPassword}><p className={`btn s square ${this.state.passwordType === 'text' ? 'success':''}`}><i className="icon-eye" /></p></a>
                    </label>
                    {
                      this.state.capsLockOn ? (
                        <div className="info info-warning"><p>{I18n.t("signup.capsLockIsOn")}</p></div>
                      ) : null
                    }
                    {this.errorsFor("password_confirmation")}
                  </li>

                </ul>
                <div className="form vvv">
                  <div className="checkbox">
                    <div className="custom-checkbox">
                        <input type="checkbox" id="user[policy_rule_age]" name="user[policy_rule_age]" value="on" />
                        <label htmlFor="user[policy_rule_age]" className="check-box"></label>
                    </div>
                    <label htmlFor="user[policy_rule_age]">{I18n.t("signup.check_old")}</label>
                  </div>
                  {this.errorsFor("policy_rule_age")}
                  <div className="checkbox">
                    <div className="custom-checkbox">
                        <input id="user[policy_rule_privacy_terms]" type="checkbox" name="user[policy_rule_privacy_terms]" value="on"  />
                        <label htmlFor="user[policy_rule_privacy_terms]" className="check-box"></label>
                    </div>
                    <label htmlFor="user[policy_rule_privacy_terms]">{I18n.t("signup.check_terms1")}  <a target="_blank" rel="noopener noreferrer" href="https://preyproject.com/terms">
                    {I18n.t("signup.button_terms")} </a> {I18n.t("signup.check_terms2")}  <a target="_blank" rel="noopener noreferrer" href="https://preyproject.com/privacy">{I18n.t("signup.button_privacy")} </a>.</label>
                  </div>
                  {this.errorsFor("policy_rule_privacy_terms")}
                  {this.errorsFor("error")}

                  <div className="checkbox">
                    <div className="custom-checkbox">
                        <input id="user[offers]" type="checkbox" name="user[offers]" value="on"  />
                        <label htmlFor="user[offers]" className="check-box"></label>
                    </div>
                    <label htmlFor="user[offers]">{I18n.t("signup.check_offers")}</label>
                  </div>

                </div>
                <div className="button-group">
                  <Link to="#" value="Log in"  onClick={this.handleClick} className="btn cta">
                    <h2>{I18n.t("signup.button_submit")}</h2>
                  </Link>
                </div>
              </form>
              <p className="here">{I18n.t('signin.maybe')}</p>
              <Link to={`/${I18n._locale}/signin`} className="btn-link btn-block" >{I18n.t("signup.button_signin")}</Link>
              <a className="btn-link btn-block" href="https://panel.preyproject.com/forgot">{I18n.t("button_forgot")}</a>
              {
                this.state.isLoading ? (
                  <Loading><code>{I18n.t(`dialog_login.loading`)}</code></Loading>
                ) : null
              }
              {
                this.state.isEmailOk ? (
                <Dialog
                className={`dialog`}
                >
                  <div className="inner" key={'pin'}>
                    <div className="content">
                      <h3>{I18n.t("signup.send_activation_title")}</h3>
                      <h5>{I18n.t("signup.send_activation_a")} {this.state.mail} {I18n.t("signup.send_activation_b")}</h5>
                    </div>
                      <Group>
                        <Group>
                          <Link onClick={this.handleModalCancel} to="#" className="btn alt">{I18n.t("signup.send_activation_cancel")}</Link>
                          <Link onClick={this.handleClick2} to="#" className="btn success icon-right">{I18n.t("signup.send_activation_send")}</Link>
                        </Group>
                      </Group>
                  </div>
                </Dialog>

                ) : null
              }
            </div>
            <div id="footer">
              <footer className="copy">
                <div className="footer-links">
                  <a href="https://preyproject.com/terms" target="_blank" rel="noopener noreferrer">{I18n.t("button_terms")}</a>
                  <a href="https://preyproject.com/privacy" target="_blank" rel="noopener noreferrer">{I18n.t("button_privacy")}</a>
                  <a href="https://www.preyproject.com/privacy#cookies" target="_blank" rel="noopener noreferrer">{I18n.t("button_cookies")}</a>
                </div>
                  <img src={logomono} alt="Prey" className="logo" />
              </footer>
            </div>
          </div>
        </div>
      </div>
    </div>

  }

}
