import React from 'react'
import logomono from '../images/branding/prey-logo-monotono-alt.svg'
import { I18n } from 'react-i18nify-lite'
import { Link } from 'react-router-dom'
import { Group } from 'prey-stash'
import { Dialog } from '../assets/Dialog'

export default class Signup extends React.Component {

  constructor(props){
    super(props)
    this.state = {
      data: {},
      isEmailSent: false,
      EmailValidation: false,
      EmailExpired: false,
      EmailConfirm: false,
      emailValue: ''
    }
  }
  handleEmailChecker = (e) => {
    e.preventDefault()
    this.setState({
      isEmailSent: !this.state.isEmailSent
    })
  }

  handleisEmailSent = (e) => {
    e.preventDefault()
    this.setState({
      isEmailSent: true,
      emailValue: document.getElementById('email').value
    })
  }

  handleEmailValidation = (e) => {
    e.preventDefault()
    this.setState({
      isEmailSent: false,
      EmailValidation: true
    })
  }

  handleEmailValue = (e) => {
    this.setState({
      emailValue: this.refs.email.value
    })
  }

  errorsFor = (name)=>{
    const errors = this.state.data[name]
    if(!errors) return
    if(errors.length === 0) return

    return <span ref='error' className="msg info-error">{
      errors.map((o)=>(
          <span>{o}</span>
        )
      )}
      </span>

  }

  handleFocus = (e) => {
    this.setState({data: {}})
  }

  handleClickSignup = (e)=>{
    var name=encodeURIComponent(document.getElementById('name').value);
    var email=encodeURIComponent(document.getElementById('email').value);
    var password1=encodeURIComponent(document.getElementById('password1').value);
    var password2=encodeURIComponent(document.getElementById('password2').value);
    var policy_rule_age=document.getElementById('user[policy_rule_age]').checked;
    var policy_rule_privacy_terms=document.getElementById('user[policy_rule_privacy_terms]').checked;
    var policy_rule_offers=document.getElementById('user[offers]').checked;
    var url = 'iossignup://parameters?emailSignup='+email+'&pwd1Signup='+password1+'&pwd2Signup='+password2+'&nameSignup='+name+'&ageSignup='+policy_rule_age+'&termsSignup='+policy_rule_privacy_terms+'&offers='+policy_rule_offers;
    this.openCustomURLinIFrame(url);

  }

  handleClickCheckSignup = (e)=>{
    var name=encodeURIComponent(document.getElementById('name').value);
    var email=encodeURIComponent(document.getElementById('email').value);
    var password1=encodeURIComponent(document.getElementById('password1').value);
    var password2=encodeURIComponent(document.getElementById('password2').value);
    var policy_rule_age=document.getElementById('user[policy_rule_age]').checked;
    var policy_rule_privacy_terms=document.getElementById('user[policy_rule_privacy_terms]').checked;
    var policy_rule_offers=document.getElementById('user[offers]').checked;
    var url = 'ioschecksignup://parameters?emailSignup='+email+'&pwd1Signup='+password1+'&pwd2Signup='+password2+'&nameSignup='+name+'&ageSignup='+policy_rule_age+'&termsSignup='+policy_rule_privacy_terms+'&offers='+policy_rule_offers;
    this.openCustomURLinIFrame(url);

  }

  handleShowTerms = (e)=>{
      var url = 'iosterms://';
      this.openCustomURLinIFrame(url);
  }

  handleShowPrivacy = (e)=>{
      var url = 'iosprivacy://';
      this.openCustomURLinIFrame(url);
  }

  openCustomURLinIFrame = (src)=> {
      var rootElm = document.documentElement;
      var newFrameElm = document.createElement("IFRAME");
      newFrameElm.setAttribute("src",src);
      rootElm.appendChild(newFrameElm);
      //remove the frame now
      newFrameElm.parentNode.removeChild(newFrameElm);
  }

  render() {
    return <div className="inner-content">
      <div className="content">
        <div className="login fs-onboarding regular inverted">
          <div className="column">
            <div className="sign-form">


              <form autoComplete="off" className="form form-session" id="new-session"acceptCharset="UTF-8">



                <h1>{I18n.t("signup.title")}</h1>
                <p className="lead">{I18n.t("signup.subtitle")}</p>
                <ul className="form vvv">
                  <li>
                    <label htmlFor="name">{I18n.t("signup.field1")}</label>
                    <input id="name" name="name" required type="text" tabIndex="1" onFocus={this.handleFocus}  />
                    {this.errorsFor("name")}
                  </li>
                  <li>
                    <label htmlFor="email">{I18n.t("signup.field2")}</label>
                    <input id="email" name="email" required type="email" tabIndex="2" onFocus={this.handleFocus} />
                    {this.errorsFor("email")}
                  </li>
                </ul>
                <ul className="form hvv">
                  <li>
                    <label className="tag-label" htmlFor="password">{I18n.t("signup.field3")}</label>
                    <input type="password" name="password1" id="password1" tabIndex="3" onFocus={this.handleFocus}/>
                    {this.errorsFor("password")}
                  </li>
                  <li>
                    <label className="tag-label" htmlFor="password">{I18n.t("signup.field4")}</label>
                    <input type="password" name="password2" id="password2" tabIndex="4" onFocus={this.handleFocus} />
                    {this.errorsFor("password_confirmation")}
                  </li>
                </ul>
                <ul className="form vvv">
                  <div className="checkbox">
                    <div className="custom-checkbox">
                        <input type="checkbox" id="user[policy_rule_age]" name="user[policy_rule_age]" value="on" />
                        <label htmlFor="user[policy_rule_age]" className="check-box"></label>
                    </div>
                    <label style={{textTransform: 'none'}} htmlFor="user[policy_rule_age]">{I18n.t("signup.check_old")}</label>
                  </div>
                  {this.errorsFor("policy_rule_age")}
                  <div className="checkbox">
                    <div className="custom-checkbox">
                        <input id="user[policy_rule_privacy_terms]" type="checkbox" name="user[policy_rule_privacy_terms]" value="on"  />
                        <label htmlFor="user[policy_rule_privacy_terms]" className="check-box"></label>
                    </div>
                    <label style={{textTransform: 'none'}} htmlFor="user[policy_rule_privacy_terms]">{I18n.t("signup.check_terms1")}  <a onClick={this.handleShowTerms} rel="noopener noreferrer">
                    {I18n.t("signup.button_terms")} </a> {I18n.t("signup.check_terms2")}  <a onClick={this.handleShowPrivacy} rel="noopener noreferrer">{I18n.t("signup.button_privacy")} </a>.</label>
                  </div>
                  {this.errorsFor("policy_rule_privacy_terms")}
                  <div className="checkbox">
                    <div className="custom-checkbox">
                        <input id="user[offers]" type="checkbox" name="user[offers]" value="on"  />
                        <label htmlFor="user[offers]" className="check-box"></label>
                    </div>
                    <label htmlFor="user[offers]">{I18n.t("signup.check_offers")}</label>
                  </div>
                  {this.errorsFor("policy_rule_offers")}

                </ul>
                <div className="button-group">
                  <Link to="#" value="Log in"  onClick={this.handleClickCheckSignup} className="btn cta">
                    <span>{I18n.t("signup.button_submit")}</span>
                  </Link>
                  <button id="btnEmailValidation" className="hidden" onClick={this.handleisEmailSent}></button>
                </div>
                
              </form>
               
              <Link to={`/${I18n._locale}/signin`} className="btn-link btn-block" >{I18n.t("signup.button_signin")}</Link>
            </div>
            <div id="footer">
              <footer className="copy">
                <div className="footer-links">
                  <a onClick={this.handleShowTerms} rel="noopener noreferrer">{I18n.t('button_terms')}</a>
                  <a onClick={this.handleShowPrivacy} rel="noopener noreferrer">{I18n.t('button_privacy')}</a>
                  <a href="https://www.preyproject.com/privacy#cookies" target="_blank" rel="noopener noreferrer">{I18n.t("button_cookies")}</a>
                </div>
                <img src={logomono} alt="Prey" className="logo" />
              </footer>
            </div>
          </div>
        </div>
        {
          this.state.isEmailSent ? (
            <Dialog
              className={`dialog`}
              >
              <div className="inner" key={'pin'}>
                <div className="content">
                  <h5>{I18n.t(`email_validation.sent.title`)}</h5>
                  <p>{I18n.t(`email_validation.sent.head_a`)}<b>{this.state.emailValue}</b>{I18n.t(`email_validation.sent.head_b`)}</p>
                </div>
                <Group>
                  <Group>
                    <a onClick={() => this.setState({isEmailSent: false}) } className="btn default">{I18n.t(`cancel`)}</a>
                    <a onClick={this.handleClickSignup} className="btn">{I18n.t(`send`)}</a>
                  </Group>
                </Group>
              </div>
            </Dialog>
          ): null
        }
        {this.state.EmailValidation ? (
          <Dialog
            className={`dialog`}
            >
            <div className="inner" key={'pin'}>
              <div className="content">
                <h5>{I18n.t(`email_validation.sent.title`)}</h5>
                <p>{I18n.t(`email_validation.sent.dialog_title`)} <b>{this.state.emailValue}</b> {I18n.t(`email_validation.sent.dialog_body`)}</p>
              </div>
              <Group>
                <Group>
                  <a onClick={() => this.setState({EmailValidation: false, EmailExpired: true})} className="btn">{I18n.t(`send`)}</a>
                </Group>
              </Group>
            </div>
          </Dialog>
        ): null}

        {this.state.EmailExpired ? (
            <Dialog
              className={`dialog`}
              >
              <div className="inner" key={'pin'}>
                <div className="content">
                  <h5>{I18n.t(`email_validation.expired.title`)}</h5>
                  <p>{I18n.t(`email_validation.expired.body`)}</p>
                </div>
                <Group>
                  <Group>
                    <a onClick={() => this.setState({EmailExpired: false, EmailConfirm: true})} className="btn">{I18n.t(`email_validation.expired.button`)}</a>
                  </Group>
                </Group>
              </div>
            </Dialog>
          ): null
        }
        {
          this.state.EmailConfirm ? (
            <Dialog
              className={`dialog`}
              >
              <div className="inner" key={'pin'}>
                <div className="content">
                  <h5>{I18n.t(`email_validation.ok.title`)}</h5>
                  <p>{I18n.t(`email_validation.ok.body`)}</p>
                </div>
                <Group>
                  <Group>
                    <a onClick={() => this.setState({EmailConfirm: false})} className="btn">{I18n.t(`email_validation.ok.button`)}</a>
                  </Group>
                </Group>
              </div>
            </Dialog>
          ): null
        }
      </div>
    </div>

  }

}
