import React, { Component } from 'react'
import { I18n } from 'react-i18nify-lite'
import { Dialog, Loading } from '../assets/Dialog'
import { Link } from 'react-router-dom'
import { motion } from 'framer-motion'
import { Group, Space, Layout } from '../assets/stash'

const initName = () => {
  openCustomURLinIFrame2("iosnamedevice://parameters?openPanelWeb=true");
  return ""
}

const openCustomURLinIFrame2 = (src)=> {
  var rootElm = document.documentElement;
  var newFrameElm = document.createElement("IFRAME");
  newFrameElm.setAttribute("src",src);
  rootElm.appendChild(newFrameElm);
  //remove the frame now
  newFrameElm.parentNode.removeChild(newFrameElm);
}

const Logo = ({ duration = 2, scale = 0.8, stroke = "rgba(255,255,255,0.40)", fill = "transparent" }) => {
  return (
    <svg className="iso" viewBox="273.029 152.065 128 128">
      <motion.path
        initial={{ opacity: 0, pathLength: 0, pathOffset: 1 }}
        animate={{ opacity: 1, pathLength: 1, pathOffset: [1, 0], scale }}
        exit={{ opacity: 0 }}
        transition={{ duration, ease: "easeOut" }}
        d="M337.033 155.783l-20.214 11.67 13.475 7.776 6.72-3.89 6.754 3.886 13.471-7.78-20.206-11.662zm.004 120.564l50.522-29.17v-31.113l-13.478 7.78.004 15.556-37.051 21.393-37.054-21.397V223.84l-13.471-7.776v31.113l50.528 29.17zm57.257-95.292v.011l.007 15.549-47.163 27.23 13.463 7.78-23.568 13.613-23.576-13.613 13.471-7.78-47.163-27.23.007-15.538-6.743-3.898v-19.443l57.257 33.052 6.747-3.886 6.728 3.886 57.268-33.063v19.443l-6.735 3.887z"
        stroke={stroke}
        fill={fill}
        strokeWidth="1"
      />
    </svg>
  );
}

class Home extends Component {
  constructor(props) {
    super(props)
    this.state = {
      showDialog: false,
      isLoading: false,
      handleView:"",
      passwordType: 'password',
      name: initName(),
    }
  }

  changeName = (ev) =>{
    ev.preventDefault()
    this.setState ({
      name: '',
    })
  }

  handleRename = (ev) => {
    //this.props.history.push(`/${I18n._locale}/rename`);
    ev.preventDefault()
    if (!this.state.showDialog) {this.openCustomURLinIFrame("ioschecktouchid://parameters?openPanelWeb=rename");}
    this.setState({
      showDialog: !this.state.showDialog,
      handleView: "iosrenamepwd://parameters?pwdLogin="
    })
  }

  handleLogin = (ev) => {
    ev.preventDefault()
    if (!this.state.showDialog) {this.openCustomURLinIFrame("ioschecktouchid://parameters?openPanelWeb=panel");}
    this.setState({
      showDialog: !this.state.showDialog,
      handleView: "iospanelpwd://parameters?pwdLogin="
    })
  }

  handleSettings = (ev) => {
    ev.preventDefault()
    if (!this.state.showDialog) {this.openCustomURLinIFrame("ioschecktouchid://parameters?openPanelWeb=setting");}
    this.setState({
      showDialog: !this.state.showDialog,
      handleView: "iossettingspwd://parameters?pwdLogin="
    })
  }

  handleCloseAccount = (ev) => {
    ev.preventDefault()
    if (!this.state.showDialog) {this.openCustomURLinIFrame("ioschecktouchid://parameters?openPanelWeb=close");}
    this.setState({
      showDialog: !this.state.showDialog,
      handleView: "iosclosepwd://parameters?pwdLogin="
    })
  }

  handleChangeName = (e) => {
    e.preventDefault()
    this.setState({
      name: document.getElementById('nametext').value
    })
  }

  handleShowPassword = (e) => {
    var type=this.state.passwordType
    if (type === 'password') {
      this.setState({
        passwordType: 'text'
      })
    } else {
      this.setState({
        passwordType: 'password'
      })
    }
  }

  openCustomURLinIFrame = (src)=> {
      var rootElm = document.documentElement;
      var newFrameElm = document.createElement("IFRAME");
      newFrameElm.setAttribute("src",src);
      rootElm.appendChild(newFrameElm);
      //remove the frame now
      newFrameElm.parentNode.removeChild(newFrameElm);
  }

  render() {
    console.log( 'yapue');
    return (
      <Layout scrollableFix={true} className="activation home fs-onboarding regular inverted">
        <div className="container main-info">
          <div className="column">
            <div className="device-name">
              <p>{I18n.t(`device_name`)}</p><span id="name_device_0">
              <a onClick={this.handleRename} className="btn icon-pencil"><span id="name_device_1"></span></a></span>
            </div>
            
            <h2>
              <small>{I18n.t(`current_status`)}</small>
              <br/>
              <span id="txtStatusDevice">{I18n.t(`status.protected`)}</span>
            </h2>
            <figure className="anime uno">
              <figure className="anime dos">
                <figure className="anime tres">
                  <Logo />
                </figure>
              </figure>
            </figure>
          </div>
        </div>
        <Group className="homeOptions">
          <Group className="ctas" id="ctas">
            <a onClick={this.handleLogin} href="#/" className="btn flex icon-off">{I18n.t(`login`)}</a>
            <a onClick={this.handleSettings} href="#/" className="btn default flex icon-cog">{I18n.t(`settings`)}</a>
          </Group>
          <Group className="info-options">
            <Link to={`/${I18n._locale}/whatsprey`} className="btn alt icon-help-circle">
              {I18n.t(`whatsprey`)}
            </Link>
            <a onClick={this.handleCloseAccount} href="#" className="btn alt icon-spy">
              {I18n.t(`close_account`)}
            </a>
          </Group>
        </Group>
        {this.state.showDialog ? (
          <Dialog className={`dialog`}>
            <div className="inner" key={'pin'}>
              <a onClick={this.handleLogin} className="close btn transparent default-text icon-cancel" />
              <h6>{I18n.t(`dialog_login.title`)}</h6>
              <form autoComplete="off" className="form form-session" id="new-session" acceptCharset="UTF-8" >
                <ul className="form hvv">
                  <li className="superpassword">
                    <label className="tag-label" htmlFor="passlogin">{I18n.t("dialog_login.holder3")}
                      <input id="passlogin" tabIndex="1" placeholder=" " type={this.state.passwordType}/>
                      <a onClick={this.handleShowPassword}><p className={`btn s square ${this.state.passwordType === 'text' ? 'success':''}`}><i className="icon-eye" /></p></a>
                   </label>
                   </li>
                </ul>
              </form>
              <Group className="vspace">
                {/* eslint-disable-next-line */}
                <a onClick={this.handleLogin} className="btn default" id="cancelBtn">{I18n.t(`dialog_login.cancel`)}</a>
                <Space />
                {/* eslint-disable-next-line */}
                <a onClick={(e) => this.openCustomURLinIFrame(this.state.handleView+encodeURIComponent(document.getElementById('passlogin').value))} className="btn icon-off">
                <h3>{I18n.t(`dialog_login.submit`)}</h3>
                </a>
              </Group>
                  <a onClick={(e) => this.openCustomURLinIFrame("iosforgot://")} className="btn alt fill icon-export">
                  {I18n.t(`dialog_login.forgot`)}
                  </a>
            </div>
          </Dialog>

        ) : null }
        {/*
          // Loading with timeout
          <Loading className="loading" onLoad={this.loadingTimeout(2000)}><code>loading</code></Loading>
        */}
        {
          this.state.isLoading ? (
            <Loading className="loading"><code>loading</code></Loading>
          ) : null
        }
        <div id="overall" />

      </Layout>

    );
  }
}

export default Home;