import React from 'react'
import styled from 'styled-components'
import { motion } from 'framer-motion'
import { darken, transparentize, adjustHue, readableColor, lighten } from 'polished'
import {
  variantsItem
} from '../Segundo'

const fadeout = transparentize

const Button = React.forwardRef(function Button(
  { ...props },
  ref
) {
  return (
    <ButtonS variants={variantsItem} {...props}>
      {props.children}
    </ButtonS>
  );
});

const InnerStyle = (props) => {
  return `
    display: inline-block;
    cursor: pointer;
    background-image: linear-gradient(150deg, ${props.linkColor}, ${adjustHue(25, props.linkColor)});
    color: ${readableColor(props.bg, '#cc0000', '#0000cc')};
    border-radius: 0;
    margin: 0;
    outline: none;
    border: none;
    overflow: hidden;
    padding: 1rem 2rem;
    text-shadow: none;
    vertical-align: bottom;
    user-select: none;
    position: relative;
    line-height: 1;
    transition: all ease 200ms;
    transform-origin: center;
    transform-style: preserve-3d;
    * { outline: none; }
    br { height: 0; margin: 0; }
    h1, h2, h3, small, p {
      margin: 0;
      line-height: 1;
      display: block;
    }
    &.btn-facebook, &[facebookLike] {
      background: linear-gradient(150deg, #384e8a 0%, ${darken(0.2, '#384e8a')} 100%);
    }
    &.btn-twitter, &[twitterLike]{
      background: linear-gradient(150deg, #1DA1F3 0%, ${darken(0.2, '#1DA1F3')} 100%);
    }
    &.btn-google, &[googleLike] {
      background: linear-gradient(150deg, #DB4437 0%, ${darken(0.2, '#DB4437')} 100%);
    }
    &.success, &[success] {
      background: linear-gradient(150deg, ${props.green} 0%, ${adjustHue(25, props.green)} 100%);
      color: ${readableColor(props.green)};
    }
    &.warning, &[warning] {
      background: linear-gradient(150deg, ${props.orange}, ${adjustHue(25, props.orange)} );
      color: ${readableColor(props.orange)};
    }
    &.error, &.danger, &[error], &[danger] {
      background: linear-gradient(150deg, ${props.red}, ${adjustHue(25, props.red)});
      color: ${readableColor(props.red)};
    }
    &:hover {
      transition: all ease 200ms;
      opacity: 1;
    }
    &:active {
      transition: all ease 200ms;
      opacity: 1;
    }
    &.border {
      box-shadow: 0 0 0 1px ${props.linkColor} inset;
    }
    &.default {
      background: linear-gradient(150deg, ${lighten(0.3, props.bg)}, ${props.bg});
      color: ${readableColor(props.bg)};
      &:hover {
        opacity: 1;
        background: ${props.bg};
      }
    }
    &.default2 {
      background: linear-gradient(150deg, ${lighten(0.3, props.bg)}, ${props.bg});
      color: ${readableColor(props.bg)};
      width: 10rem;
      &:hover {
        opacity: 1;
        background: ${props.bg};
      }
    }
    &.alt {
      background: ${lighten(0.05, props.bg)};
      box-shadow: 0 0 0 1px ${lighten(0.1, props.bg)} inset;
      &:hover {
        opacity: 1;
        background: transparent;
      }
    }
    &.transparent, &[transparent] {
      background: transparent;
      box-shadow: none;
      color: ${props.linkColor};
      opacity: 1;
      &:hover {
        opacity: 1;
        background: transparent;
      }
    }
    &.link{
      padding: inherit;
    }
    &.success-text {
      color: ${props.green};
    }
    &.error-text, &.danger-text {
      color: ${props.red};
    }
    &.warning-text {
      color: ${props.orange};
    }
    &.default-text {
      color: ${props.fontColor};
    }
    &.xl {
      font-size: 3.0rem;
    }
    &.l {
      font-size: 2.0rem;
    }
    &.s {
      font-size: calc((${props.fontSize} - 0.2rem) + 0.3vw);
      padding: 0.5rem 1rem;
      transform: scale(1);
    }
    &.fill {
      width: 100%;
    }
    &.flex {
      flex: 1 1 auto;
    }
    &[class^="icon-"], &[class^="icon-"], &[class*=" icon-"] {
      padding-right: 5rem;
      padding-left: 1rem;
      &:before {
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        z-index: 500;
        align-self: center;
        justify-content: center;
        margin: 0;
        padding: 1.1rem 1rem;
        width: auto;
      }
    }
    &.cta {
      text-transform: uppercase;
      font-weight: normal;
      border: none;
      text-align: left;
      clip-path: polygon(2rem 0%, 100% 0, 100% 2rem, 100% calc(100% - 2rem), calc(100% - 2rem) 100%, 0 100%, 0% calc(100% - 2rem), 0% 2rem);
      display: flex;
      flex-direction: row;
      max-width: 32rem;
      transition: all ease 300ms;
      &:hover {
        transition: all ease 300ms;
        background: linear-gradient(160deg, ${props.linkColor} 0%, ${darken(0.2, props.linkColor)} 100%);
      }
      span {
        padding: 0;
      }
      &[class^="icon-"], &[class^="icon-"], &[class*=" icon-"] {
        padding-right: 5rem;
        padding-left: 2rem;
      }
      figure {
        width: 5.2rem;
        flex: 0 0 5.2rem;
        padding: 0;
        align-self: center;
        svg, img {
          width: 100%;
          path {
            will-change: auto;
            transition: all ease 500ms;
          }
        }
      }
    }
    &.disabled, &[disabled] {
      cursor: not-allowed;
      opacity: 0.60;
    }
    &.required, &[required] {
      cursor: help;
      background: linear-gradient(150deg, ${fadeout(0.60, props.linkColor)} 0%, ${darken(0.1, fadeout(0.60, props.linkColor))} 100%);
      color: ${readableColor(fadeout(0.60, props.linkColor))};
    }
    &.square {
      padding: 1rem;
    }
    .note {
      display: flex;
      flex-direction: row-reverse;
      position: absolute;
      bottom: 1px; right: 1px;
      overflow: hidden;
      padding: 0;
      z-index: 1000;
      padding: 0.3rem 0.5rem;
      max-width: 50%;
      background-color: ${fadeout(0.60, props.bg)};
      small {
        text-transform: uppercase;
        position: relative;
        display: block;
        flex: 0 0 100%;
        text-align: right;
        align-self: flex-start;
        z-index: 100;
        font-size: 60%;
      }
    }
  `
}

const ButtonStyle = (props) => {
  return `
    .btn, button, input[type="button"], input[type="submit"], input[type="reset"] {
      ${InnerStyle(props)}
    }
`}

const ButtonS = styled(motion.button)`
  ${ props => InnerStyle(props.theme) }
`

export {Button, ButtonStyle};
