import React from 'react'
import { Link } from 'react-router-dom'
import logomono from '../images/branding/prey-logo-monotono-alt.svg'
import { I18n } from 'react-i18nify-lite'
import { Loading } from '../assets/Dialog'

const handleClickQR = (e)=>{
  if(window.Android){
    window.Android.qr();
  } else {
    alert("ola oso qr" )
  }
}

export default class Login extends React.Component {

  constructor(props){
    super(props)
    this.state = {
      data: {},
      isLoading: false,
      passwordType: 'password',
      capsLockOn: false,
    }
  }

  errorsFor = (name) => {
      const errors = this.state.data[name]
      if(!errors) return
      if(errors.length === 0) return

      return <div className="info info-error">{
        errors.map((o,i)=>(
            <p key={i}>{o}</p>
          )
        )}
        </div>
  }

  handleClickLogin = (e)=>{
    this.setState({
        data: {} ,
        isLoading: true
      }, ()=>{setTimeout(this.callHandleClickLogin, 0)
    })
  }
  callHandleClickLogin = (e)=>{
    var emaillogin=document.getElementById('emaillogin').value
    var passlogin=document.getElementById('passlogin').value
    if(window.Android){
        let json=window.Android.mylogin(''+emaillogin,''+passlogin);
        if(json!==''){
          this.setState({
              data: JSON.parse(json),
              isLoading: false
            })
        }else{
          this.props.history.push(`/${I18n._locale}/activation`);
        }
    } else {
      let json='{"error":["Credenciales invlidas"]}';
      this.setState({
        data: JSON.parse(json),
        isLoading: false
      })
      //this.props.history.push(`/${I18n._locale}/activation`);
      //alert("ola oso emaillogin:"+emaillogin+"  passlogin:"+passlogin )
    }
  }

  handleFocus = (e) => {
    this.setState({data: {}})
  }

  handleShowPassword = (e) => {
    var type=this.state.passwordType
    if (type === 'password') {
      this.setState({
        passwordType: 'text'
      })
    } else {
      this.setState({
        passwordType: 'password'
      })
    }
  }

  verificateTab = (counter) =>{
    counter=counter-1;
    this.changeTab()
    if(counter>0){
      if(window.Android){
        setTimeout(
          () => this.verificateTab(counter),
          1000
        );
      }else{
        alert("verificateTab:"+counter);
        setTimeout(
          () => this.verificateTab(counter),
          1000
        )
      }
    }
  }

  touch = () => {
    if(window.Android){
      window.Android.touch();
    }
    this.setState({
        capsLockOn: false
    });
    if(window.Android){
      this.verificateTab(20)
    }else{
      this.verificateTab(1)
    }
  }

  changeTab = () => {
    var capsLock=this.state.capsLockOn
    if(window.Android){
      capsLock=window.Android.capsLockOn();
    }else{
      capsLock=!capsLock;
    }
    this.setState({
      capsLockOn: capsLock
    })
  }

  render() {
    return  <div className="login fs-onboarding regular inverted">
    <div className="column">
      <div className="sign-form">
        <form autoComplete="off" className="form form-session" id="new-session" acceptCharset="UTF-8" onFocus={this.handleFocus}>
          <h1>{I18n.t('signin.button_submit')}</h1>
          <p className="lead">{I18n.t("signin.subtitle")}</p>

          <ul className="form vvv">

                  <li>
                    <label htmlFor="emaillogin">{I18n.t("signin.field1")}</label>
                    <input id="emaillogin" name="email" placeholder=" " required type="email" tabIndex="1" onFocus={this.handleFocus} />
                    {this.errorsFor("email")}
                  </li>
                </ul>

           <ul className="form hvv">
                  <li className="superpassword">
                    <label className="tag-label" htmlFor="passlogin">{I18n.t("signin.field2")}
                      <input type={this.state.passwordType} name="password1" id="passlogin" tabIndex="2" onFocus={this.handleFocus}  onClick={this.touch} />
                      <a onClick={this.handleShowPassword}><p className={`btn s square ${this.state.passwordType === 'text' ? 'success':''}`}><i className="icon-eye" /></p></a>
                    </label>
                    {
                      this.state.capsLockOn ? (
                        <div className="info info-warning"><p>{I18n.t("signup.capsLockIsOn")}</p></div>
                      ) : null
                    }
                    {this.errorsFor("error")}
                  </li>


                </ul>


          <Link tabIndex="3" to="#" onClick={this.handleClickLogin} className="btn cta">
            <h2>{I18n.t('signin.button_submit')}</h2>
          </Link>
          <p />
          <Link tabIndex="4" to="#" onClick={handleClickQR} className="btn alt icon-qr-code">{I18n.t('signin.button_qr')}</Link>
        </form>

          <p className="here">{I18n.t('signin.maybe')}</p>
          <Link className="btn-link btn-block" to={`/${I18n._locale}/signup`}>{I18n.t('signin.button_account')}</Link>
          <a className="btn-link btn-block" href="https://panel.preyproject.com/forgot">{I18n.t('button_forgot')}</a>

          {
            this.state.isLoading ? (
              <Loading><code>{I18n.t(`dialog_login.loading`)}</code></Loading>
            ) : null
          }

      </div>


      <div id="footer">
        <footer className="copy">
          <div className="footer-links">
            <a href="https://preyproject.com/terms" target="_blank" rel="noopener noreferrer">{I18n.t('button_terms')}</a>
            <a href="https://preyproject.com/privacy" target="_blank" rel="noopener noreferrer">{I18n.t('button_privacy')}</a>
            <a href="https://www.preyproject.com/privacy#cookies" target="_blank" rel="noopener noreferrer">{I18n.t('button_cookies')}</a>
          </div>
            <img src={logomono} alt="Prey" className="logo" />
        </footer>
      </div>
    </div>
  </div>
 }

}

