// Segundo
// UI Kit

import { lighten, adjustHue, readableColor, setHue, desaturate, shade, transparentize } from "polished"

export const composeRefs = (...args) => {
  return ref => {
    args.forEach(arg => {
      if (!arg) {
        return;
      }

      if (typeof arg === "function") {
        arg(ref);
        return;
      }

      arg.current = ref;
    });
  };
}

export const transition = {
  duration: 0.5,
  ease: [0.43, 0.13, 0.23, 0.96],
  staggerChildren: 0.1,
  delayChildren: 0.2
}

export const variantsParent = {
  initial: { opacity: 0, height: 0, transition: {...transition} },
  enter: { opacity: 1, height: "auto", transition: {...transition} },
  exit: { opacity: 0, height: 0, transition }
}

export const variantsItem = {
  initial: {
    x: 100,
    opacity: 0,
    transition: {
      x: { stiffness: 1000 }
    }
  },
  enter: {
    x: 0,
    opacity: 1,
    transition: {
      x: { stiffness: 1000, velocity: -100 }
    }
  },
  exit: {
    x: 100,
    opacity: 0,
    transition: {
      x: { stiffness: 1000 }
    }
  }
}

const createTheme = (opts={}) => {

  const name = opts.name || 'segundo'

  const desaturation = opts.desaturation || 0.3
  const lightness = opts.lightness || 0.1
  const hue = opts.hue || 0

  const processLight = lighten(lightness, '#2552be');
  const processSaturation = desaturate(desaturation, processLight);
  const colorBasePalette = setHue(hue, processSaturation);

  const red = opts.red || adjustHue(hue + 0, colorBasePalette);
  const orange = opts.orange || adjustHue(hue + 30, colorBasePalette);
  const yellow = opts.yellow || adjustHue(hue + 60, colorBasePalette);
  const lime = opts.lime || adjustHue(hue + 90, colorBasePalette);
  const green = opts.green || adjustHue(hue + 120, colorBasePalette);
  const cyan = opts.cyan || adjustHue(hue + 150, colorBasePalette);
  const aqua = opts.aqua || adjustHue(hue + 180, colorBasePalette);
  const blue = opts.blue || adjustHue(hue + 210, colorBasePalette);
  const teal = opts.teal || adjustHue(hue + 240, colorBasePalette);
  const purple = opts.purple || adjustHue(hue + 270, colorBasePalette);
  const magenta = opts.magenta || adjustHue(hue + 300, colorBasePalette);
  const violet = opts.violet || adjustHue(hue + 330, colorBasePalette);

  const linkColor = opts.linkColor || processLight
  const bg = opts.bg || shade(0.8, linkColor)
  const fontColor = opts.fontColor || readableColor(bg)
  const fg = opts.fg || readableColor(bg)

  const fontSize = opts.fontSize || '1.8rem'
  const baseLine = opts.baseLine || '1.1'

  const fontFamily = opts.fontFamily || `Tahoma, Geneva, sans-serif`
  const mono = opts.mono || `"Lucida Console", Monaco, monospace`
  const headings = opts.headings || fontFamily

  const background = opts.background || lighten(0.1, bg) || lighten(0.1, opts.bg)
  const water = opts.water || bg || opts.bg
  const landuse = opts.landuse || lighten(0.1, bg) || lighten(0.1, opts.bg)
  const grass = opts.grass || green || opts.green
  const road = opts.road || shade(0.3, linkColor) || opts.linkColor

  return {
    name,
    bg,
    fg,
    fontColor,
    linkColor,
    desaturation,
    lightness,
    hue,
    processLight,
    processSaturation,
    colorBasePalette,
    fontSize,
    baseLine,
    fontFamily,
    headings,
    mono,
    red,
    orange,
    yellow,
    lime,
    green,
    teal,
    aqua,
    blue,
    cyan,
    purple,
    magenta,
    violet,
    background,
    water,
    landuse,
    grass,
    road
  }
}

const Reset = (theme) => {
  return`
    :root {
      font-size: 62.5%;
    }
    h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, cite, code, del, dfn, em, img, q, s, samp, small, strike, strong, sub, sup, tt, var, dd, dl, dt, li, ol, ul, fieldset, form, label, legend, button, table, caption, tbody, tfoot, thead, tr, th, td, figure { margin: 0; padding: 0; border: 0; font-weight: normal; font-style: normal; line-height: 1; font-family: inherit; text-align: left; }
    table { border-collapse: collapse; border-spacing: 0; }
    ol, ul { list-style: none; }
    q:before, q:after, blockquote:before, blockquote:after { content: ""; }
    article, aside, details, figcaption, figure, footer, header, hgroup, nav, section, summary {
      display: block;
    }
    audio, canvas, video {
      display: inline-block;
    }
    audio:not([controls]) {
      display: none;
      height: 0;
    }
    [hidden], .hidden {
      display: none;
    }
    *, *:before, *:after {
      -webkit-box-sizing: inherit;
      -moz-box-sizing: inherit;
      box-sizing: inherit;
    }
    html {
      box-sizing: border-box;
      margin: 0; padding: 0;
    }
    html, body {
      height: 100%;
      overflow-x: hidden;
      background: ${theme.bg};
    }
  `
}

