import React from 'react'
import { I18n } from 'react-i18nify-lite'
import { Dialog, Loading } from './assets/Dialog'
import { Link } from 'react-router-dom'
import { motion } from 'framer-motion'
import icoHelp from './images/onboarding/ico-help.svg'
import { Group, Space, Layout } from './assets/stash'




const initName = () => {
  if(window.Android){
    return window.Android.initName();
  }else{
    return 'oso';
  }
}
const initUserFree = () => {
  if(window.Android){
    return window.Android.initUserFree();
  }else{
    return true;
  }
}
const initContactFormForFree = () => {
  if(window.Android){
    return window.Android.initContactFormForFree();
  }else{
    return true;
  }
}
const Logo = ({ duration = 2, scale = 0.8, stroke = "rgba(255,255,255,0.40)", fill = "transparent" }) => {
  return (
    <svg className="iso" viewBox="273.029 152.065 128 128">
      <motion.path
        initial={{ opacity: 0, pathLength: 0, pathOffset: 1 }}
        animate={{ opacity: 1, pathLength: 1, pathOffset: [1, 0], scale }}
        exit={{ opacity: 0 }}
        transition={{ duration, ease: "easeOut" }}
        d="M337.033 155.783l-20.214 11.67 13.475 7.776 6.72-3.89 6.754 3.886 13.471-7.78-20.206-11.662zm.004 120.564l50.522-29.17v-31.113l-13.478 7.78.004 15.556-37.051 21.393-37.054-21.397V223.84l-13.471-7.776v31.113l50.528 29.17zm57.257-95.292v.011l.007 15.549-47.163 27.23 13.463 7.78-23.568 13.613-23.576-13.613 13.471-7.78-47.163-27.23.007-15.538-6.743-3.898v-19.443l57.257 33.052 6.747-3.886 6.728 3.886 57.268-33.063v19.443l-6.735 3.887z"
        stroke={stroke}
        fill={fill}
        strokeWidth="1"
      />
    </svg>
  );
}

class Android extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      showDialog: false,
      tipo: '',
      twoStep: false,
      data: {},
      isLoading: false,
      name: initName(),
      passwordType: 'password',
      capsLockOn: false,
      userFree: initUserFree(),
      contact: initContactFormForFree(),
    }
  }

  errorsFor = (name) => {
    const errors = this.state.data[name]
    if(!errors) return
    if(errors.length === 0) return

    return <div className="info info-error">{
      errors.map((o,i)=>(
          <p key={i}>{o}</p>
        )
      )}
      </div>
  }

  handleClose= (ev) => {
    this.setState({
      showDialog: false
    })
  }

  handleRename = (ev) => {
    let isPowerOk=false;
    if(window.Android){
      isPowerOk=window.Android.isTimePasswordOk()
    }else{
      isPowerOk=false;
    }
    if(!isPowerOk){
      this.callHandleRename()
    }else{
      this.props.history.push(`/${I18n._locale}/rename`);
    }
  }

  callHandleRename = (ev) => {
    this.setState({
        data: {},
    })
    if(window.Android){
      if(window.Android.getTwoStepEnabled()){
        this.setState({
          twoStep:true,
        })
      }else{
        this.setState({
          twoStep:false,
        })
      }
    }else{
      this.setState({
        twoStep:false,
      })
    }
    this.setState({
      tipo: 'rename',
      showDialog: !this.state.showDialog
    })
  }

  handleLogin = (ev) => {
    let isPowerOk=false;
    if(window.Android){
      isPowerOk=window.Android.isTimePasswordOk()
    }else{
      isPowerOk=false;
    }
    if(!isPowerOk){
      this.callHandleLogin()
    }else{
      if(window.Android){
        window.Android.openPanelWeb()
      } else {
        alert('open panel web');
      }
    }
  }
  callHandleLogin = (ev) => {
    this.setState({
        data: {},
    })
    if(window.Android){
      if(window.Android.getTwoStepEnabled()){
        this.setState({
          twoStep:true,
        })
      }else{
        this.setState({
          twoStep:false,
        })
      }
    }else{
      this.setState({
        twoStep:false,
      })
    }
    this.setState({
      tipo: 'login',
      showDialog: !this.state.showDialog
    })
  }

  handleSettings = (ev) => {
    let isPowerOk=false;
    if(window.Android){
      isPowerOk=window.Android.isTimePasswordOk()
    }else{
      isPowerOk=false;
    }
    if(!isPowerOk){
      this.callHandleSettings()
    }else{
      this.props.history.push(`/${I18n._locale}/security`);
    }
  }

  callHandleSettings = (ev) => {
    if(window.Android){
      if(window.Android.getTwoStepEnabled()){
        this.setState({
          twoStep:true,
        })
      }else{
        this.setState({
          twoStep:false,
        })
      }
    }else{
      this.setState({
        twoStep:true,
      })
    }
    this.setState({
      tipo:'setting',
      showDialog: !this.state.showDialog
    })

  }
  handlePassword = (ev) => {
    this.setState(
      {
        data: {} ,
        isLoading: true
      }, ()=>{setTimeout(this.callLogin, 0) }
    )
  }

  handleShowPassword = (e) => {
    var type=this.state.passwordType
    if (type === 'password') {
      this.setState({
        passwordType: 'text'
      })
    } else {
      this.setState({
        passwordType: 'password'
      })
    }
  }

  touch = () => {
    if(window.Android){
      window.Android.touch();
    }
    this.setState({
        capsLockOn: false
    });
    if(window.Android){
      this.verificateTab(20)
    }else{
      this.verificateTab(1)
    }
  }

  verificateTab = (counter) =>{
    counter=counter-1;
    this.changeTab()
    if(counter>0){
      if(window.Android){
        setTimeout(
          () => this.verificateTab(counter),
          1000
        );
      }else{
        alert("verificateTab:"+counter);
        setTimeout(
          () => this.verificateTab(counter),
          1000
        )
      }
    }
  }

  changeTab = () => {
    var capsLock=this.state.capsLockOn
    if(window.Android){
      capsLock=window.Android.capsLockOn();
    }else{
      capsLock=!capsLock;
    }
    this.setState({
      capsLockOn: capsLock
    })
  }

  handleChangeInput = () =>{
    this.setState({data: {}})
  }

  callLogin  = ()=>{
    var passlogin=document.getElementById('passlogin').value
    var passlogin2=""
    try{
      passlogin2=document.getElementById('passlogin2').value
    }catch(error) {}
    if(window.Android){
      let json=window.Android.login_tipo(''+passlogin,passlogin2,this.state.tipo);
      this.setState(
        {
          data: JSON.parse(json),
          isLoading: false
        },()=>{setTimeout(this.callExit,0,json) }
      )
    } else{
        //let json='{"error":["Credenciales invlidas"]}'
        let json='{"result":true}'
        setTimeout(()=>{
          this.setState(
            {
              data: JSON.parse(json),
              isLoading: false
            },()=>{setTimeout(this.callExit, 0,json)}
          )
        }, 5000)
    }
  }

  callExit  = (jsonSt)=>{

    let result=false
    try{
      var json=JSON.parse(jsonSt)
      if(jsonSt.includes("result"))
        result=json.result
      if(result){
        if(this.state.tipo==='setting'){
          this.props.history.push(`/${I18n._locale}/security`);
        }else{
          this.props.history.push(`/${I18n._locale}/rename`);
        }
      }
    }catch(err){
    }

  }
  handleForgot = (ev) => {
    if(window.Android){
      window.Android.forgot()
    } else{
      alert("ola forgot")
    }
  }

  handleFocus = (e) => {
    this.setState({data: {}})
  }

  loadingTimeout = (time) => {
    if (time) {
      clearTimeout(time);
    }
    time = setTimeout(() => {
      this.setState({
        isLoading: false
      })
    }, time);
  }

  handleChangeInput = () =>{
   
    var passlogin=document.getElementById('passlogin').value;
    if(window.Android){
      window.Android.inputwebview(passlogin,this.state.tipo);
    }else{
      alert('passlogin:'+passlogin+" protected tipo:"+this.state.tipo);
    }
  }

  handleClickHelp = (e)=>{
    if(window.Android){
      window.Android.clickHelp();
    }
    this.props.history.push(`/${I18n._locale}/help`);
  }
   

  render() {

    return (
      <Layout scrollableFix={true} className="activation home fs-onboarding regular inverted">
        
        {
          this.state.contact ? (
            <a onClick={this.handleClickHelp}>
              <div className="btn fill help-center">
                <img src={icoHelp} alt="Help" className="help-ico" />
                <span className="tac">{I18n.t("helpcenter.title")}</span>
              </div>
            </a>
         ):null
        }
        
        <div className="container main-info">
          <div className="column">
          <div className="device-name">
              <p>{I18n.t(`device_name`)}</p>
              <a onClick={this.handleRename} className="btn icon-pencil">{ this.state.name }</a>
          </div>
          <h2>
           <small>{I18n.t(`current_status`)}</small>
           <br/>
           <span>{I18n.t(`status.protected`)}</span>
          </h2>

            {!this.state.showDialog&&(
              <figure className="anime uno">
                <figure className="anime dos">
                  <figure className="anime tres">
                    <Logo />
                  </figure>
                </figure>
              </figure>
            )}
          </div>
        </div>
        <Group className="homeOptions">
          <Group className="ctas">
            {/* eslint-disable-next-line */}
            <a onClick={this.handleLogin} className="btn flex icon-off">
              <span>{I18n.t(`login`)}</span>
            </a>
            {/* eslint-disable-next-line */}
            <a onClick={this.handleSettings} className="btn default flex icon-cog">
              <span>{I18n.t(`settings`)}</span>
            </a>
          </Group>
          <Group className="info-options">
            <Link to={`/${I18n._locale}/whatsprey`} className="btn alt icon-help-circle">
              <small>{I18n.t(`whatsprey`)}</small>
            </Link>
            <Link to={`/${I18n._locale}/privacy`} className="btn alt icon-spy">
              <small>{I18n.t(`your_privacy`)}</small>
            </Link>
          </Group>
          {this.state.userFree ? (
            <a href={I18n.t(`uninstall_prey_link`)} className="btn uninstall-info">
              <small className="tac">{I18n.t(`uninstall_prey`)}</small>
            </a>
          ) : null}
        </Group>
        {this.state.showDialog ? (
          <Dialog
            className={`dialog`}
            >
            <div className="inner" key={'pin'}>
              {/* eslint-disable-next-line */}
              <a onClick={this.handleClose} className="close btn transparent default-text icon-cancel" />
              <h6>{I18n.t(`dialog_login.title`)}</h6>
              <form autoComplete="off" className="form form-session" id="new-session" acceptCharset="UTF-8"  onFocus={this.handleFocus}>
                 <ul className="form hvv">
                  <li className="superpassword">
                    <label className="tag-label" htmlFor="passlogin">{I18n.t("dialog_login.holder3")}
                      <input type={this.state.passwordType} name="password1" id="passlogin" tabIndex="1" placeholder=" " onFocus={this.handleFocus}  onClick={this.touch} onChange={this.handleChangeInput}/>
                      <a onClick={this.handleShowPassword}><p className={`btn s square ${this.state.passwordType === 'text' ? 'success':''}`}><i className="icon-eye" /></p></a>
                    </label>
                    {
                      this.state.capsLockOn ? (
                      <div className="info info-warning"><p>{I18n.t("signup.capsLockIsOn")}</p></div>
                      ) : null
                    }
                  </li>
                </ul>
                {this.state.twoStep ? (
                <ul className="form hvv">
                  <li className="superpassword">
                    <label className="tag-label" htmlFor="passlogin2">{I18n.t("dialog_login.title2")}
                      <input type={this.state.passwordType} name="password2" id="passlogin2" tabIndex="2" placeholder=" " onFocus={this.handleFocus}  onClick={this.touch} />
                      <a onClick={this.handleShowPassword}><p className={`btn s square ${this.state.passwordType === 'text' ? 'success':''}`}><i className="icon-eye" /></p></a>
                    </label>
                    {
                      this.state.capsLockOn ? (
                      <div className="info info-warning"><p>{I18n.t("signup.capsLockIsOn")}</p></div>
                      ) : null
                    }
                  </li>
                </ul>
                  ) : null }
              </form>
              {this.errorsFor("error")}
              <Group className="vspace">
                {/* eslint-disable-next-line */}
                <a onClick={this.handleClose} className="btn default">{I18n.t(`dialog_login.cancel`)}</a>
                <Space />
                {/* eslint-disable-next-line */}
                <a onClick={this.handlePassword} className="btn icon-off">
                  <h3>{I18n.t(`dialog_login.submit`)}</h3>
                </a>
              </Group>
              <Link onClick={this.handleForgot} to={`/${I18n._locale}/`} className="btn alt fill icon-export">
                {I18n.t(`dialog_login.forgot`)}
              </Link>
            </div>
          </Dialog>
        ) : null }
        {
          this.state.isLoading ? (
            <Loading><code>{I18n.t(`dialog_login.loading`)}</code></Loading>
          ) : null
        }
      </Layout>
    );
  }
}

export default Android;