import React from 'react'
import { I18n } from 'react-i18nify-lite'
import { Loading } from '../assets/Dialog'

const initName = () => {
  openCustomURLinIFrame2("iosnamedevice://parameters?openPanelWeb=true");
  return ""
}

const openCustomURLinIFrame2 = (src)=> {
  var rootElm = document.documentElement;
  var newFrameElm = document.createElement("IFRAME");
  newFrameElm.setAttribute("src",src);
  rootElm.appendChild(newFrameElm);
  //remove the frame now
  newFrameElm.parentNode.removeChild(newFrameElm);
}

export default class Lock extends React.Component {

  constructor(props){
    super(props)
    this.state = {
      data: {},
      exists: false,
      name: initName(),
      isLoading: false,
      nameError:'',
      handleView:"iosrename://parameters?pwdLogin=",
    }
  }

   errorsFor = (name) => {
    const errors = this.state.data[name]
    if(!errors) return
    if(errors.length === 0) return

    return <div className="info info-error">{
      errors.map((o,i)=>(
          <p key={i}>{o}</p>
        )
      )}
    </div>
  }

  handleClick = (ev) => {
    this.setState(
      {
        isLoading: true
      }, ()=>{setTimeout(this.callNewName, 2000) }
    )
  }

  changeName = (ev) =>{
    ev.preventDefault()
    this.setState ({
      name: '',
    })
  }

  handleChangeName = (e) => {
    e.preventDefault()
    this.setState({
      name: document.getElementById('nametext').value
    })
  }

  callNewName = (e) => {
    var newName2=document.getElementById('newName').value;
    this.setState({
      isLoading: false,
    })
    if(window.Android){
      let json=window.Android.rename2(newName2);
      if(json!==''){
        var obj=JSON.parse(json);
        if(obj.code===200){
          this.props.history.push(`/${I18n._locale}/`);
        }else{
          this.setState({
            nameError:obj.error,
            exists: true,
          })
        }
      }
    }else{
      let json='{"code":200,"error":"error oso","name":""}';
      if(json!==''){
        var obj2=JSON.parse(json);
        if(obj2.code===422){
          this.props.history.push(`/${I18n._locale}/`);
        }else{
          this.setState({
            nameError:obj2.error,
            exists: true,
          })
        }
      }
    }
  }

  handleClickRename = (e)=>{
    var newName=encodeURIComponent(document.getElementById('newName').value)
    var url = 'iosrename://parameters?newName='+newName;
    this.openCustomURLinIFrame(url);
  }

  handleClickCancel = (ev) => {
    //this.props.history.push(`/${I18n._locale}/index`);
    var url = 'iosindex://parameters';
    this.openCustomURLinIFrame(url);
  }

  openCustomURLinIFrame = (src)=> {
      var rootElm = document.documentElement;
      var newFrameElm = document.createElement("IFRAME");
      newFrameElm.setAttribute("src",src);
      rootElm.appendChild(newFrameElm);
      //remove the frame now
      newFrameElm.parentNode.removeChild(newFrameElm);
  }

  render() {
    return  <div className="home fs-onboarding regular inverted">
    <div className="column">
      <div className="rename-form">
        <h4 className="heading2">{I18n.t("rename.heading")}</h4>
        <p>{I18n.t("rename.body")} </p>
        <form autoComplete="off" className="form form-session" acceptCharset="UTF-8">
        <ul className="form vvv">
          <li>
            <label htmlFor="currentName">{I18n.t("rename.current")}</label>
            <input className="disabled" id="currentName" name="text" disabled={true} type="text" value={this.state.name} />
          </li>
          <li>
            <label htmlFor="newName">{I18n.t("rename.write")}</label>
            <input id="newName" name="newName" placeholder="" required type="text" tabIndex="1" onFocus={this.handleFocus} onChange={this.handleChange}/>
            {this.errorsFor("email")}
            {
            this.state.exists ? (<div className="info info-error"><p>{ this.state.nameError}</p></div>) : null
            }
          </li>
        </ul>
        </form>
        <a className="btn flex success full" to="#"  onClick={this.handleClickRename}>
          <span>{I18n.t("rename.confirm")}</span>
        </a>
        <br/><br/>
        <a className="btn flex default full" to="#"  onClick={this.handleClickCancel}>
          <span>{I18n.t("rename.cancel")}</span>
        </a>
        <input type="hidden" id="nametext" value=""/><button id="btnChangeName" className="hidden" onClick={this.handleChangeName}></button>
      </div>
    </div>
    {
      this.state.isLoading ? (
        <Loading><code>{I18n.t(`dialog_login.loading`)}</code></Loading>
      ) : null
    }
    
  </div>
 }

}
