import React from 'react'
import { Link } from 'react-router-dom'
import { Input, Group, Space } from '../assets/stash'
import { Dialog } from '../assets/Dialog'
import { I18n } from 'react-i18nify-lite'

const initRunBackground = () => {
  if(window.Android){
    return window.Android.initBackground()
  } else {
    return true
  }
}

const initPin = () => {
  if(window.Android){
    return window.Android.initPin()
  } else {
    return true
  }
}

const initPin4 = () => {
  if(window.Android){
    return window.Android.initPin4()
  } else {
    return 1333
  }
}


const initShield = () => {
  if(window.Android){
    return window.Android.initShield()
  } else {
    return false
  }
}

const initVersion = () => {
  if(window.Android){
    return window.Android.initVersion()
  } else {
    return "2.3.4"
  }
}

const initXiaomi = () => {
  if(window.Android){
    return window.Android.initXiaomi()
  } else {
    return false
  }
}

const initHuawei = () => {
  if(window.Android){
    return window.Android.initHuawei()
  } else {
    return true
  }
}

const options = [
  { value: '0', label: ""},
  { value: '1', label: ""},
  { value: '2', label: ""},
  { value: '3', label: ""},
  { value: '4', label: ""},
  { value: '5', label: ""},
  { value: '6', label: ""},

];

const initScheduler = () => {
  options[0].label=I18n.t('scheduled.zero');
  options[1].label=I18n.t('scheduled.one');
  options[2].label=I18n.t('scheduled.two');
  options[3].label=I18n.t('scheduled.three');
  options[4].label=I18n.t('scheduled.four');
  options[5].label=I18n.t('scheduled.five');
  options[6].label=I18n.t('scheduled.six');
  if(window.Android){
    return window.Android.initScheduler();
  } else {
    return 3
  }
}

let showDialogAndroid9 = false
let showDialog = false
let plisactive = initPin()
let showErrorPin = false

class Security extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
       
      runBackground: initRunBackground(),

      runShield: initShield(),
       
      version:initVersion(),
      scheduler:initScheduler(),
      xiaomi_alert:initXiaomi(),
      huawei_alert:initHuawei(),
    }
  }

  componentDidMount () {
    this.setState({
      runBackground: this.changeRunBackground(),

      runShield: this.changeShield(),
    })
  }

  report() {
    if(window.Android){
      window.Android.report()
    } else {
      alert("ola oso report:" )
    }
  }

  wipe() {
    if(window.Android){
      window.Android.wipe()
    } else {
      alert("ola oso wipe:" )
    }
  }

  changeRunBackground = () => {
    var runnBg=document.getElementById('run-in-bg').checked
    this.setState({
      runBackground: runnBg
    })
    if(window.Android){
      window.Android.setBackground(runnBg)
    }
    return runnBg
  }


  changeShield = () => {
    this.setState({
      runShield: !this.state.runShield
    })
  }

  changeShield2 = () => {
    this.setState({
      runShield: !this.state.runShield
    })
    var shield=document.getElementById('shield-off-btn').checked
    if(window.Android){
      if(window.Android.versionIsPieOrAbove()){
        showDialogAndroid9 =true
        document.getElementById('shield-off-btn').checked =false
        this.setState({
          runShield: false
        })
      } else {
        window.Android.setShieldOf(shield)
      }
    } else {
      showDialogAndroid9 =false
      document.getElementById('shield-off-btn').checked =false
        this.setState({
          runShield: false
        })
        alert("ola oso shield:"+shield )
    }
  }

  handleModalOpen = (ev) => {
    showDialog =true
  }

  handleModalCancel = (ev) => {
    showDialog = false
  }


  handleModalCancel9 = (ev) => {
    showDialogAndroid9 = false
  }


  handleSavePin = () => {
    var pin_=document.getElementById('passlogin').value
    showDialog = true
    if(pin_!==''){
      if(pin_.length===4){
        this.setState({
          pin4: pin_
        })
        if(window.Android){
          window.Android.savepin2(pin_)
        } else {
          alert('ola oso save PIN:'+pin_)
        }
        plisactive = true
        showDialog =false
      }else{
        showErrorPin=true;
        this.setState({
          showErrorPin: true
        })
      }
    }else{
      showErrorPin=true;
      this.setState({
          showErrorPin: true
      })
    }
  }

  handleDelete = () =>{
      if(window.Android){
        window.Android.savepin2('')
      } else {
        alert('ola oso handleDelete')
      }
      document.getElementById('shield-off-btn').checked =false

      this.setState({
        runShield: false
      })
      plisactive = false
      showDialog = false

  }

  handleChangeInput = () =>{
    showErrorPin=false;
    this.setState({
          showErrorPin: false
    })
    var pin_=document.getElementById('passlogin').value;
    var len_=pin_.length;
    if(len_>4){
      pin_=pin_.substring(0,4);
    }
    document.getElementById('passlogin').value=pin_;
  }

  changeScheduler = selectedOption => {
    this.setState({
      scheduler: document.getElementById('scheduler_').value
    })
    if(window.Android){
        window.Android.changeScheduler(""+document.getElementById('scheduler_').value);
    } else {
        alert("changeScheduler:"+document.getElementById('scheduler_').value);
    }
  }

  render() {
    return (
      <div className="security fs-onboarding regular inverted">
        <div className="column">
          {(this.state.xiaomi_alert) ? (
            <div className="alert-grid">
              <figure className="icon-alert">
                  <i className="icon-attention"/>
              </figure>
              <h5 className="title-alert">{I18n.t("securitypage.xiaomi_alert.title")}</h5>
              <p className="description-alert" dangerouslySetInnerHTML={{__html: I18n.t('securitypage.xiaomi_alert.description')}}/>
              <a href={I18n.t(`securitypage.xiaomi_alert.link`)} className="btn uninstall-info button-grid">
                <small className="tac">{I18n.t(`securitypage.xiaomi_alert.button`)}</small>
              </a>
            </div>
          ):null}
          {(this.state.huawei_alert) ? (
            <div className="alert-grid">
              <figure className="icon-alert">
                  <i className="icon-attention"/>
              </figure>
              <h5 className="title-alert">{I18n.t("securitypage.huawei_alert.title")}</h5>
              <p className="description-alert" dangerouslySetInnerHTML={{__html: I18n.t('securitypage.huawei_alert.description')}}/>
              <a href={I18n.t(`securitypage.huawei_alert.link`)} className="btn uninstall-info button-grid">
                <small className="tac">{I18n.t(`securitypage.huawei_alert.button`)}</small>
              </a>
            </div>
          ):null}
          <h4 className="heading">{I18n.t("securitypage.title")}</h4>
          <p>{I18n.t("securitypage.subtitle")}</p>
          <div className="item">
            <div className="row">
              <figure>
                <i className="icon-panel-settings"/>
              </figure>
              <h5>{I18n.t("securitypage.background_title")}</h5>
              <div className="toggle">
                <input
                  type="checkbox"
                  name="run-in-bg"
                  className="toggle-checkbox"
                  id="run-in-bg"
                  onChange={this.changeRunBackground}
                  defaultChecked={this.state.runBackground}
                />
                <label className="toggle-label" htmlFor="run-in-bg">
                    <span className="toggle-inner"></span>
                    <span className="toggle-switch"></span>
                </label>
              </div>
            </div>
            <p>{I18n.t("securitypage.background_subtitle")}</p>
          </div>
          <div className="item">
            <div className="row">
              <figure>
                <i className="icon-trash"/>
              </figure>
              <h5>{I18n.t("securitypage.wipe_title")}</h5>
              <Link to="#" onClick={this.wipe} className="btn">{I18n.t("securitypage.wipe_button")}</Link>
            </div>
            <span>{I18n.t("securitypage.wipe_subtitle")}</span>
          </div>
          <div className="line-through" />



          <div className="item">
            <div className="row">
              <figure>
                <i className="icon-login-box-lock"/>
              </figure>
              <h5>{I18n.t("securitypage.setup_pin_title")}</h5>
              <Link to="#" onClick={this.handleModalOpen}  className="btn">
              {plisactive ?  I18n.t("securitypage.setup_pin_activated"):I18n.t("securitypage.setup_pin_activate")}
              </Link>
            </div>
            <p>{I18n.t("securitypage.setup_pin_subtitle")}</p>
          </div>
          <div className={`prey-pin ${!plisactive ? 'inactive' : 'active' }`}>
            <i className="reference-icon icon-login-box-lock" />
             
            <div className="item">
              <div className="row">
                <figure>
                  <i className="icon-lock-shield" />
                </figure>
                <h5>{I18n.t("securitypage.shield_title")}</h5>
                <div className="toggle">
                  <input type="checkbox" name="shield-off-btn" className="toggle-checkbox" id="shield-off-btn" onChange={this.changeShield2} checked={this.state.runShield} />
                  <label className="toggle-label" htmlFor="shield-off-btn">
                      <span className="toggle-inner"></span>
                      <span className="toggle-switch"></span>
                  </label>
                </div>
              </div>
              <p>{I18n.t("securitypage.shield_subtitle")}.</p>
            </div>




          </div>
        <div className="line-through" />
        
          <div className="item">
            <div className="row">
              <figure>
                <i className="icon-panel-settings"/>
              </figure>
              <h5>{I18n.t("scheduled.title")}</h5>
              {/*
                <Select id="scheduler_" options={options} onChange={this.changeScheduler}  value={this.state.scheduler}></Select>
                */}
              <select id="scheduler_" defaultValue={this.state.scheduler} onChange={this.changeScheduler}>
                {options.map(option => (
                  <option
                    key={option.value}
                    value={option.value}
                    onClick={() => this.changeScheduler(option)}>
                    {option.label}
                  </option>
                ))}
              </select>
            </div>
            <span>{I18n.t("scheduled.body")}</span>
          </div>
        <div className="line-through" />

          <div className="item">
            <div className="row">
              <figure>
                <i className="icon-help-circle"/>
              </figure>
              <h5>{I18n.t("help.title")}</h5>
            </div>
            <span>{I18n.t("help.body")}
              <a href={I18n.t("help.link")} className="b">{I18n.t("help.description")}</a>
            </span>
          </div>
       
          
          <div className="line-through" />
            <div className="item">
              <div className="row">
                <h5>{I18n.t("securitypage.prey_for")}</h5>
              </div>
              <span>Version <small><code>{this.state.version}</code></small> - Prey Inc.</span>
          </div>
        </div>
        <Group className="homeOptions">
          <Link to={`/${I18n._locale}/`} className="btn fill default">
            <i className="icon-cancel" />
            <span>{I18n.t("close")}</span>
          </Link>
        </Group>
        {showDialog &&(
          <Dialog className="dialog">
            <div className="inner" key={'pin'}>
              {showErrorPin  ? (<div ref='error' className="info info-error">{I18n.t("securitypage.setup_pin_error")}</div>) : null }
              <Input
                label={I18n.t("securitypage.dialog_4pin.title")}
                className="mount"
                id="passlogin"
                placeholder={plisactive ? this.state.pin4 : I18n.t("securitypage.dialog_4pin.holder")}
                type="number"
                onChange={this.handleChangeInput}
                />
              <Group className="vspace">
                <Link onClick={this.handleModalCancel} to="#" className="btn default">{I18n.t("securitypage.dialog_4pin.cancel")}</Link>
                <Space />
                <Link onClick={this.handleSavePin} to="#" className="btn">{I18n.t("securitypage.dialog_4pin.submit")}</Link>
              </Group>
              {plisactive &&(
                <Link to="#" onClick={this.handleDelete}  className="btn fill transparent error-text s icon-trash">{I18n.t("securitypage.dialog_4pin.remove_pin")}</Link>
              )}
            </div>
          </Dialog>
        )}
        {showDialogAndroid9 &&(
          <Dialog className="dialog message">
            <div className="inner" key={'alert'}>
              <h3>{I18n.t("securitypage.not_android9_title")}</h3>
              <p>{I18n.t("securitypage.not_android9_subtitle")}</p>
              <Link onClick={this.handleModalCancel9} to="#" className="btn default">{I18n.t("securitypage.not_android9_close")}</Link>
            </div>
          </Dialog>
        )}


      </div>
    )
  }
}

export default Security
